import * as React from 'react';
import { useQuery } from '@tanstack/react-query';

import MapComponent from './mapComponent';
import Loader from '../components/Loader';
import { TrucksForMapQuery } from '../api';
import { TruckForMap } from '../utils/dataTypes';

export default function GeneralMap() {
  const {
    isLoading: isTruckListLoading,
    isFetching: isTruckListFetching,
    data: truckListData,
  } = useQuery({
    queryKey: ['trucksForMap'],
    queryFn: TrucksForMapQuery,
    keepPreviousData: false,
    retry: false,
  });
  const rows: TruckForMap[] = (truckListData || []).filter(
    (truck) => truck.lastLocation,
  );

  return (
    <>
      <MapComponent truckListData={rows} />
      <Loader visible={isTruckListLoading || isTruckListFetching} />
    </>
  );
}
